/* ============================================
   REAL-TIME TESTIMONIALS - PREMIUM 3D CAROUSEL
   ============================================ */

   .testimonials-wrapper {
    padding: 80px 20px;
    position: relative;
    overflow: hidden;
  }
  
  /* Ambient Background */
  .testimonials-wrapper::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    height: 600px;
    background: radial-gradient(
      circle,
      rgba(142, 45, 226, 0.12) 0%,
      rgba(74, 0, 224, 0.05) 40%,
      transparent 70%
    );
    pointer-events: none;
    animation: ambientPulse 8s ease-in-out infinite;
  }
  
  @keyframes ambientPulse {
    0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 0.5; }
    50% { transform: translate(-50%, -50%) scale(1.2); opacity: 0.8; }
  }
  
  /* ============================================
     HEADER
     ============================================ */
  
  .testimonials-header {
    text-align: center;
    margin-bottom: 50px;
    position: relative;
    z-index: 2;
  }
  
  .testimonials-badge {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 8px 20px;
    background: linear-gradient(135deg, rgba(142, 45, 226, 0.2), rgba(74, 0, 224, 0.2));
    border: 1px solid rgba(142, 45, 226, 0.3);
    border-radius: 50px;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 2px;
    color: #a78bfa;
    margin-bottom: 20px;
  }
  
  .live-indicator {
    width: 8px;
    height: 8px;
    background: #22c55e;
    border-radius: 50%;
    animation: livePulse 2s ease-in-out infinite;
  }
  
  @keyframes livePulse {
    0%, 100% { opacity: 1; box-shadow: 0 0 0 0 rgba(34, 197, 94, 0.5); }
    50% { opacity: 0.8; box-shadow: 0 0 0 6px rgba(34, 197, 94, 0); }
  }
  
  .testimonials-title {
    font-size: 48px;
    font-weight: 700;
    color: #fff;
    margin: 0 0 15px 0;
    line-height: 1.2;
  }
  
  .gradient-text {
    background: linear-gradient(135deg, #667eea, #764ba2, #f472b6);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    animation: gradientShift 5s ease infinite;
    background-size: 200% 200%;
  }
  
  @keyframes gradientShift {
    0%, 100% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
  }
  
  .testimonials-subtitle {
    font-size: 16px;
    color: rgba(255, 255, 255, 0.6);
    margin: 0;
  }
  
  /* ============================================
     LOADING STATE
     ============================================ */
  
  .testimonials-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 80px 20px;
    gap: 20px;
  }
  
  .loading-spinner {
    width: 50px;
    height: 50px;
    border: 3px solid rgba(142, 45, 226, 0.2);
    border-top-color: #8b5cf6;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
  
  .testimonials-loading p {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
  }
  
  /* ============================================
     CAROUSEL
     ============================================ */
  
  .testimonials-carousel {
    position: relative;
    max-width: 600px;
    margin: 0 auto;
    padding: 40px 20px;
    cursor: grab;
    user-select: none;
    -webkit-user-select: none;
  }
  
  .testimonials-carousel.is-dragging {
    cursor: grabbing;
  }
  
  .testimonials-track {
    position: relative;
    height: 380px;
    display: flex;
    align-items: center;
    justify-content: center;
    perspective: 1200px;
    transform-style: preserve-3d;
  }
  
  /* ============================================
     CARD STYLES
     ============================================ */
  
  .tcard {
    position: absolute;
    width: 100%;
    max-width: 400px;
    height: 340px;
    border-radius: 24px;
    background: linear-gradient(
      145deg,
      rgba(30, 30, 50, 0.9) 0%,
      rgba(20, 20, 35, 0.95) 100%
    );
    border: 1px solid rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    transition: transform 0.5s cubic-bezier(0.23, 1, 0.32, 1),
                opacity 0.4s ease,
                filter 0.4s ease,
                box-shadow 0.4s ease;
    will-change: transform, opacity;
    transform-style: preserve-3d;
    overflow: hidden;
  }
  
  .tcard-active {
    box-shadow:
      0 30px 60px -15px rgba(0, 0, 0, 0.5),
      0 0 40px rgba(142, 45, 226, 0.15),
      inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }
  
  /* Glow Effect */
  .tcard-glow {
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: conic-gradient(
      from 0deg,
      transparent,
      rgba(142, 45, 226, 0.1),
      transparent,
      rgba(102, 126, 234, 0.1),
      transparent
    );
    animation: glowRotate 8s linear infinite;
    opacity: 0;
    transition: opacity 0.4s ease;
  }
  
  .tcard-active .tcard-glow {
    opacity: 1;
  }
  
  @keyframes glowRotate {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
  
  /* Card Content */
  .tcard-content {
    position: relative;
    z-index: 2;
    padding: 30px;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  /* Quote */
  .tcard-quote {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 80px;
    font-family: Georgia, serif;
    color: rgba(142, 45, 226, 0.15);
    line-height: 1;
    pointer-events: none;
  }
  
  /* Stars */
  .tcard-stars {
    display: flex;
    gap: 4px;
    margin-bottom: 15px;
  }
  
  .tcard-star {
    color: #fbbf24;
    font-size: 16px;
    animation: starPop 0.4s ease backwards;
    text-shadow: 0 0 10px rgba(251, 191, 36, 0.5);
  }
  
  @keyframes starPop {
    0% { transform: scale(0); opacity: 0; }
    50% { transform: scale(1.3); }
    100% { transform: scale(1); opacity: 1; }
  }
  
  /* Text */
  .tcard-text {
    font-size: 16px;
    line-height: 1.7;
    color: rgba(255, 255, 255, 0.85);
    margin: 0;
    flex-grow: 1;
    font-style: italic;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
  }
  
  /* Author */
  .tcard-author {
    display: flex;
    align-items: center;
    gap: 15px;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.08);
    margin-top: auto;
  }
  
  .tcard-avatar {
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    overflow: hidden;
  }
  
  .tcard-avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .anonymous-avatar {
    font-size: 28px;
  }
  
  .tcard-avatar-ring {
    position: absolute;
    inset: -3px;
    border-radius: 50%;
    background: linear-gradient(135deg, #667eea, #764ba2);
    z-index: -1;
    animation: ringPulse 3s ease-in-out infinite;
  }
  
  @keyframes ringPulse {
    0%, 100% { opacity: 0.5; transform: scale(1); }
    50% { opacity: 1; transform: scale(1.05); }
  }
  
  .tcard-author-info {
    flex-grow: 1;
    min-width: 0;
  }
  
  .tcard-author-info h4 {
    font-size: 15px;
    font-weight: 600;
    color: #fff;
    margin: 0 0 4px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .tcard-author-info p {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }
  
  .tcard-company {
    color: #a78bfa;
  }
  
  .tcard-date {
    font-size: 11px;
    color: rgba(255, 255, 255, 0.3);
  }
  
  .verified-badge {
    width: 22px;
    height: 22px;
    background: linear-gradient(135deg, #22c55e, #16a34a);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    color: white;
    flex-shrink: 0;
  }
  
  /* Reflection */
  .tcard-reflection {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60%;
    background: linear-gradient(to top, rgba(255, 255, 255, 0.02), transparent);
    pointer-events: none;
  }
  
  /* ============================================
     SWIPE HINT
     ============================================ */
  
  .swipe-hint {
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.4);
    transition: opacity 0.3s ease;
  }
  
  .swipe-hint.hidden {
    opacity: 0;
  }
  
  .swipe-icon {
    animation: swipeMotion 1.5s ease-in-out infinite;
  }
  
  @keyframes swipeMotion {
    0%, 100% { transform: translateX(-5px); }
    50% { transform: translateX(5px); }
  }
  
  /* ============================================
     NAVIGATION
     ============================================ */
  
  .testimonials-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 25px;
    margin-top: 45px;
  }
  
  .nav-arrow {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(255, 255, 255, 0.03);
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  
  .nav-arrow svg {
    width: 22px;
    height: 22px;
  }
  
  .nav-arrow:hover {
    background: rgba(142, 45, 226, 0.2);
    border-color: rgba(142, 45, 226, 0.5);
    color: #fff;
    transform: scale(1.1);
    box-shadow: 0 0 25px rgba(142, 45, 226, 0.3);
  }
  
  .nav-arrow:active {
    transform: scale(0.95);
  }
  
  /* Dots */
  .nav-dots {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .nav-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: none;
    background: rgba(255, 255, 255, 0.15);
    cursor: pointer;
    padding: 0;
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
  }
  
  .nav-dot:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.2);
  }
  
  .nav-dot.active {
    width: 32px;
    border-radius: 5px;
    background: rgba(142, 45, 226, 0.3);
  }
  
  .dot-progress {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 0;
    background: linear-gradient(90deg, #667eea, #764ba2);
    border-radius: 5px;
  }
  
  .nav-dot.active .dot-progress {
    animation: dotProgress 6s linear forwards;
  }
  
  @keyframes dotProgress {
    from { width: 0; }
    to { width: 100%; }
  }
  
  .more-dots {
    font-size: 11px;
    color: rgba(255, 255, 255, 0.4);
    margin-left: 5px;
  }
  
  /* Testimonial Count */
  .testimonials-count {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    margin-top: 25px;
    color: rgba(255, 255, 255, 0.4);
    font-size: 13px;
  }
  
  .count-number {
    font-weight: 700;
    color: #a78bfa;
    font-size: 16px;
  }
  
  /* ============================================
     ADD TESTIMONIAL BUTTON
     ============================================ */
  
  .add-testimonial-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin: 40px auto 0;
    padding: 16px 32px;
    background: linear-gradient(135deg, rgba(142, 45, 226, 0.2), rgba(74, 0, 224, 0.2));
    border: 1px solid rgba(142, 45, 226, 0.3);
    border-radius: 50px;
    color: #fff;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    overflow: hidden;
  }
  
  .add-testimonial-btn:hover {
    background: linear-gradient(135deg, rgba(142, 45, 226, 0.3), rgba(74, 0, 224, 0.3));
    border-color: rgba(142, 45, 226, 0.5);
    transform: translateY(-3px);
    box-shadow: 0 15px 40px rgba(142, 45, 226, 0.3);
  }
  
  .btn-icon {
    font-size: 20px;
  }
  
  .btn-glow {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    background: radial-gradient(circle, rgba(142, 45, 226, 0.4) 0%, transparent 70%);
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.5s ease;
    pointer-events: none;
  }
  
  .add-testimonial-btn:hover .btn-glow {
    transform: translate(-50%, -50%) scale(1);
  }
/* ============================================
   SIDE PANEL MODAL (NO BACKDROP)
   ============================================ */

   .modal-backdrop {
    position: fixed;
    inset: 0;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: flex-end; /* Align to right */
    z-index: 800;
    pointer-events: none;
  }
  
  .modal-backdrop > * {
    pointer-events: auto;
  }
  
  .testimonial-modal {
    width: 100%;
    max-width: 450px;
    height: 100vh;
    max-height: 100vh;
    overflow-y: auto;
    background: linear-gradient(
      to left,
      rgba(30, 30, 50, 0.98), 
      rgba(20, 20, 35, 0.99)
    );
    backdrop-filter: blur(20px);
    border-left: 2px solid rgba(142, 45, 226, 0.4);
    border-radius: 0; /* No radius for side panel */
    padding: 40px 35px;
    animation: slideInRight 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    box-shadow: -20px 0 60px rgba(0, 0, 0, 0.5);
  }
  
  @keyframes slideInRight {
    from {
      opacity: 0;
      transform: translateX(100%);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

.testimonial-modal {
  position: relative;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  background: linear-gradient(145deg, rgba(30, 30, 50, 0.98), rgba(20, 20, 35, 0.99));
  border: 1px solid rgba(142, 45, 226, 0.3); /* Added glow border */
  border-radius: 24px;
  padding: 35px;
  animation: modalSlide 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  /* Enhanced shadow to make modal stand out */
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.1),
    0 0 80px rgba(142, 45, 226, 0.2);
  pointer-events: auto;
}

@keyframes modalSlide {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
  







