  .modal-close:hover {
    background: rgba(239, 68, 68, 0.2);
    border-color: rgba(239, 68, 68, 0.3);
    color: #ef4444;
  }
  
  .modal-header {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .modal-icon {
    font-size: 48px;
    display: block;
    margin-bottom: 15px;
  }
  
  .modal-header h2 {
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    margin: 0 0 8px 0;
  }
  
  .modal-header p {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }
  
  /* Form Styles */
  .testimonial-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .form-toggle-group {
    display: flex;
    justify-content: center;
  }
  
  .toggle-label {
    display: flex;
    align-items: center;
    gap: 12px;
    cursor: pointer;
    padding: 12px 20px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 50px;
    transition: all 0.3s ease;
  }
  
  .toggle-label:hover {
    background: rgba(255, 255, 255, 0.08);
  }
  
  .toggle-label input {
    display: none;
  }
  
  .toggle-slider {
    width: 44px;
    height: 24px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    position: relative;
    transition: all 0.3s ease;
  }
  
  .toggle-slider::after {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    width: 20px;
    height: 20px;
    background: #fff;
    border-radius: 50%;
    transition: transform 0.3s ease;
  }
  
  .toggle-label input:checked + .toggle-slider {
    background: linear-gradient(135deg, #667eea, #764ba2);
  }
  
  .toggle-label input:checked + .toggle-slider::after {
    transform: translateX(20px);
  }
  
  .toggle-text {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.8);
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  .form-group label {
    font-size: 13px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.7);
  }
  
  .form-group input,
  .form-group textarea {
    padding: 14px 16px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    color: #fff;
    font-size: 14px;
    transition: all 0.3s ease;
    resize: none;
  }
  
  .form-group input::placeholder,
  .form-group textarea::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: rgba(142, 45, 226, 0.5);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0 0 0 3px rgba(142, 45, 226, 0.1);
  }
  
  /* Rating Input */
  .rating-input {
    display: flex;
    gap: 8px;
  }
  
  .rating-star {
    width: 40px;
    height: 40px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .rating-star:hover {
    transform: scale(1.1);
    border-color: rgba(251, 191, 36, 0.5);
  }
  
  .rating-star.active {
    color: #fbbf24;
    background: rgba(251, 191, 36, 0.15);
    border-color: rgba(251, 191, 36, 0.3);
    text-shadow: 0 0 10px rgba(251, 191, 36, 0.5);
  }
  
  /* Character Counter */
  .char-counter {
    text-align: right;
    margin-top: 5px;
  }
  
  .char-counter span {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.4);
  }
  
  .char-counter span.warning {
    color: #fbbf24;
  }
  
  /* Submit Button */
  .submit-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 16px 32px;
    background: linear-gradient(135deg, #667eea, #764ba2);
    border: none;
    border-radius: 12px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 10px;
  }
  
  .submit-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 10px 30px rgba(142, 45, 226, 0.4);
  }
  
  .submit-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  .spinner {
    width: 18px;
    height: 18px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top-color: #fff;
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
  }
  
  .form-note {
    text-align: center;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.4);
    margin: 0;
  }
  
  /* ============================================
     LIGHT THEME
     ============================================ */
  
  [data-theme="light"] .testimonials-title,
  .light-mode .testimonials-title {
    color: #1a1a2e;
  }
  
  [data-theme="light"] .testimonials-subtitle,
  .light-mode .testimonials-subtitle {
    color: rgba(0, 0, 0, 0.6);
  }
  
  [data-theme="light"] .tcard,
  .light-mode .tcard {
    background: linear-gradient(145deg, rgba(255, 255, 255, 0.95), rgba(248, 248, 255, 0.98));
    border-color: rgba(0, 0, 0, 0.08);
  }
  
  [data-theme="light"] .tcard-text,
  .light-mode .tcard-text {
    color: rgba(0, 0, 0, 0.75);
  }
  
  [data-theme="light"] .tcard-author-info h4,
  .light-mode .tcard-author-info h4 {
    color: #1a1a2e;
  }
  
  [data-theme="light"] .testimonial-modal,
  .light-mode .testimonial-modal {
    background: linear-gradient(145deg, #fff, #f8f8ff);
  }
  
  [data-theme="light"] .modal-header h2,
  .light-mode .modal-header h2 {
    color: #1a1a2e;
  }
  
  [data-theme="light"] .form-group input,
  [data-theme="light"] .form-group textarea,
  .light-mode .form-group input,
  .light-mode .form-group textarea {
    background: rgba(0, 0, 0, 0.03);
    border-color: rgba(0, 0, 0, 0.1);
    color: #1a1a2e;
  }
  
  /* ============================================
     RESPONSIVE
     ============================================ */
  
  @media (max-width: 768px) {
    .testimonials-wrapper {
      padding: 50px 15px;
    }
  
    .testimonials-title {
      font-size: 32px;
    }
  
    .testimonials-track {
      height: 360px;
    }
  
    .tcard {
      max-width: 320px;
      height: 320px;
    }
  
    .tcard-content {
      padding: 25px;
    }
  
    .tcard-text {
      font-size: 14px;
      -webkit-line-clamp: 4;
    }
  
    .form-row {
      grid-template-columns: 1fr;
    }
  
    .testimonial-modal {
      padding: 25px;
    }
  }
  
  @media (max-width: 480px) {
    .tcard {
      max-width: 290px;
      height: 330px;
    }
  
    .nav-dots {
      display: none;
    }
  
    .testimonials-nav {
      gap: 40px;
    }
  }
  
  /* ============================================
     REDUCED MOTION
     ============================================ */
  
  @media (prefers-reduced-motion: reduce) {
    .tcard,
    .nav-arrow,
    .nav-dot,
    .tcard-glow,
    .add-testimonial-btn {
      transition: none !important;
      animation: none !important;
    }
  }

  .tcard-delete-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 100; /* Make sure it's above everything */
    background: rgba(255, 0, 0, 0.2);
    border: 1px solid rgba(255, 0, 0, 0.4);
    border-radius: 50%;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.2s ease;
  }
  
  .tcard-delete-btn:hover {
    background: rgba(255, 0, 0, 0.8);
    transform: scale(1.1);
  }

  